<script>
export default {
  data() {
    return {
      navAnimate: false,
      outerAnimate: false,
      startAnimateSwitch: false,
      textStartShow: "",
      textCount: 0,
      textInArray: [
        {
          textStep: 1,
          textFlash: "是否偶爾會想著",
        },
        {
          textStep: 2,
          textFlash: "關於自己星座的",
        },
        {
          textStep: 3,
          textFlash: "運勢",
        },
        {
          textStep: 4,
          textFlash: "事業",
        },
        {
          textStep: 5,
          textFlash: "財運",
        },
        {
          textStep: 6,
          textFlash: "愛情",
        },
        {
          textStep: 7,
          textFlash: "甚至是與你喜歡的她",
        },
        {
          textStep: 8,
          textFlash: "會有什麼樣的結果",
        },
        {
          textStep: 9,
          textFlash: "或許在這裡會有些解答可以參考",
        },
        {
          textStep: 10,
          textFlash: "可點選稍後出現菜單點選你需要的內容",
        },
      ],
      routeArray: [
        {
          routeNum: 0,
          routeName: "/fortune",
        },
        {
          routeNum: 1,
          routeName: "/match",
        },
        {
          routeNum: 2,
          routeName: "/explain",
        },
      ],
      count: 0,
      animate: null,
      animateTarget: [
        {
          nodeNum: 0,
          nodeDeg: -70,
        },
        {
          nodeNum: 1,
          nodeDeg: 180,
        },
        {
          nodeNum: 2,
          nodeDeg: 70,
        },
      ],
    };
  },
  mounted() {
    setTimeout(() => {
      this.outerAnimate = true;
      this.startAnimate();
      setTimeout(() => (this.navAnimate = true), 44500);
    }, 500);
  },
  methods: {
    goHome() {
      this.$router.push("/");
      window.location.reload();
    },
    startAnimate() {
      let timerForText = setInterval(() => {
        this.textCount++;
        this.textInArray.forEach((key) => {
          if (key.textStep == this.textCount) {
            this.textStartShow = key.textFlash;
            this.startAnimateSwitch = true;
            setTimeout(() => (this.startAnimateSwitch = false), 3000);
          }
        });
        if (this.textInArray.length == this.textCount) {
          this.textCount = 0;
          clearInterval(timerForText);
        }
      }, 4000);
    },
    circleAnimateS(e) {
      this.animateTarget.forEach((key) => {
        if (key.nodeNum == e.target.dataset.num) {
          document.querySelector(
            ".circle"
          ).style.transform = `rotate(${key.nodeDeg}deg)`;
          document.querySelector(".circle").classList.add("circle-light");
        }
      });
    },
    circleAnimateE() {
      document.querySelector(".circle").style.transform = `rotate(360deg)`;
      document.querySelector(".circle").classList.remove("circle-light");
    },
    goPage(e) {
      this.routeArray.forEach((key) => {
        if (key.routeNum == e.target.dataset.num) {
          this.navAnimate = false;
          setTimeout(
            () => (document.querySelector(".navs").style.display = "none"),
            700
          );
          setTimeout(() => this.$router.push(key.routeName, () => {}), 701);
        }
      });
    },
  },
  created() {
    console.log(1);
  },
};
</script>